/*  */
.search-bar {
    margin: 20px;
    width: 100vw;

}


.search-bar input {
    width: 57vw;
    margin-right: 2px;
}

.search-bar button {
    padding: 2px 16px;
    background-color: white;
    cursor: pointer;
    border: 1px solid grey;
}

.search-bar button:hover {
    background-color: #eee;
}

.video-item img {
    width: 64px;

}

.video-detail .details {
    margin-top: 10px;
    padding: 10px;
    border: 10px solid #ddd;
}


.list-group-item {
    cursor: pointer;
    margin-top: 1rem;
    width: 100%;
    margin-left: 1rem;
}

.list-group-item:hover {
    background-color: #eee;
}
